input, button {
  border: 2px solid #f5f5f5;
  border-radius: 15px;
  outline: .5px solid gray;
  margin: 10px 5px 20px 7px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .2), 0 6px 20px rgba(0, 0, 0, .19);
}

input {
  padding: 5px;
}

button {
  color: #000;
  background-color: #0fa;
  padding: 5px 20px;
  font-size: 14px;
}

.timer {
  width: 420px;
  background-image: linear-gradient(to right, rgba(0, 255, 170, 0), #0fa);
  border: 3px solid #f5f5f5;
  border-radius: 40px;
  outline: .1px solid gray;
  justify-content: center;
  font-family: Courier New, monospace;
  display: flex;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .2), 0 6px 20px rgba(0, 0, 0, .19);
}

.field {
  flex-direction: column-reverse;
  justify-content: center;
  align-items: center;
  padding: 10px 15px;
  display: flex;
}

.label {
  color: #000;
  font-size: 16px;
  font-weight: 600;
}

.value {
  color: #f5f5f5;
  text-shadow: -1px 0 #000, 0 1px #000, 1px 0 #000, 0 -1px #000;
  font-size: 24px;
  font-weight: 700;
}

